*{
    margin: 0;
    padding: 0;
}

/* 폰트 */
a{
    font-weight: bold;
    color: black;
    text-decoration: none;
}


a:visited{
    text-decoration: none;
}


a:hover{
    color: yellowgreen;
}

h1{
    font-size: 6rem;
}

h3{
    font-size: 2rem;
}

.main p{
    font-size: 1.5rem;
}

/* 메인 */
.main{
  display: block;
  width: 100%;
  height: 100vh;
}
  
.main_img{
  width: 100%;
}
  
.main .article{
  display: table;
  min-width: 100%;
  height: 100%;
  position: absolute;
}
  
.main .article_inner{
  display: table-cell;
  text-align: center;
  vertical-align: middle;
}
  
.backf{
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
  opacity: 0.9;
}

.backb{
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -2;
}