*{
    margin: 0;
    padding: 0;
}

.nav{
  position : fixed;
  width: 100%;
}
  
/* 네비게이션 */
.navbar{
  display: flex;
  height: 5rem;
  width: 100%;
  z-index: 1;
  justify-content: space-between;
  margin: 0 auto;
  position: fixed;
}

.logo{
  width: 4.5rem;
}

.nav_menu{
  list-style: none;
  display: flex;
  font-size: 1.3rem;
  align-items: center;
}

.nav_menu li{
  padding: 6px 7px;
}

.menu{
  position: absolute;
  right: 30px;
  font-size: 2rem;
  color: white;
  display: none;
}

@media screen and (max-width: 960px){
  .navbar{
    flex-direction: column;
    align-items: flex-start;
    background: cornflowerblue;
  }

  .nav_menu{
    display: none;
    flex-direction: column;
    align-items: center;
    width: 100%;
    background: cornflowerblue;
  }

  .nav_menu li:hover{
    background-color: #ff7d78;
    border-radius: 10px;
  }

  .nav_menu li{
    width: 95%;
    text-align: center;
  }

  .nav_menu.active{
    display: flex;
  }

  .menu{
    display: block;
    top: 28%;
  }
}