*{
    padding: 0;
    margin: 0;
}

/* 폰트 */
.union p{
    font-size: 3rem;
}

/* 유니온 */
.union{
    overflow: hidden;
    display: block;
    background-size: cover;
    object-fit: cover;
}

.union_img{
    width: 100%;
}

.union .article{
    display: table;
    min-width: 100vw;
    height: 100%;
    position: absolute;
}
  
.union .article_inner{
    display: table-cell;
    text-align: center;
    vertical-align: top;
}

.backu{
    width: 100%;
    height: 100vh;
    Object-fit: none;
    z-index: -1;
    position: absolute;
}

.unionbc{
    display: table-cell;
    max-width: 99%;
    text-align: center;
}

.footer{
    font-size: 1rem;
}

.unionb{
    display: none;
}

.unionc{
    display: none;
}

@media screen and (max-width: 960px){
    .unionbc{
        display: none;
    }

    .unionb{
        display: visible;
    }

    .unionc{
        display: block;
    }
}