*{
    margin: 0;
    padding: 0;
}

ul{
    list-style: none;
}

/* 폰트 */
.contents p{
    font-size: 2.5rem;
    font-weight: bold;
}

/* 컨텐츠 */
.contents{
    overflow: hidden;
    display: block;
    background-size: cover;
    object-fit: cover;
}

.contents .article{
    margin: 10vh 0 0 0;
    width: 100%;
    height: 100vh;
    position: absolute;
    text-align: center;
}

.contents .article_inner{
    text-align: center;
    vertical-align: top;
}

.backc{
    width: 100%;
    height: 100vh;
    z-index: -1;
}

.animate{
    display: flex;
    position: relative;
    max-width: 100%;
    max-height: 100%;
    justify-content: center;
}

.animate .img{
    margin: 10vh 0 0 0;
    width: 100%;
}

.buton{
    display: flex;
    margin-top: 77vh;
    position: relative;
    justify-content: space-between;
    margin-left: 30%;
    margin-right: 30%;
}

.button1{
    display: none;
}

.button2{
    display: none;
}

button{
    background-color: rgba(0, 0,0, 0);
    border: 0;
    outline: 0;
}
